import { Component, OnDestroy, OnInit, Output, EventEmitter } from '@angular/core';
import { AbstractControl, FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { map, take, tap } from 'rxjs/operators';
import { UserQuery } from '../../state/user/user.query';

import * as crypto from 'crypto-js';

import { AuthService } from '../../services/auth.service';
import { environment } from '../../environments/environment';

@Component({
  selector: 'lib-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit, OnDestroy {

 // @Output() getToken: EventEmitter<string> =   new EventEmitter();
  @Output() getToken = new EventEmitter<any>();

  environment = environment;
  loginForm: FormGroup;
  formError = false;
  isPasswordErr = false;
  isRemainingAttempts = false;
  remainingAttemptsCount = 0;
  isLocked = false;
  errMsg = "";
  userNameError = false;
  isLoaderShow = false;

  constructor(
    private fBuilder: FormBuilder,
    private authService: AuthService,
    private userQuery: UserQuery,
    private router: Router,
    private route: ActivatedRoute
  ) { }

  ngOnInit(): void {
    this.createForm();
  }

  
  createForm() {
    this.loginForm = this.fBuilder.group({
      userName: ['', Validators.required],
      password: ['', Validators.required],
      rememberMe: [false]
    });
  }

  loginUser() {

    this.showLoader();

    if (!this.loginForm.valid) {
      this.formError = true;
    } else {
      this.formError = false;
      let pwd = crypto.AES.encrypt(this.loginForm.get('password')?.value, crypto.enc.Utf8.parse(this.environment.keys.ENCRYPT_DECRYPT_KEY), {
        iv: crypto.enc.Utf8.parse(this.environment.keys.IV),
        padding: crypto.pad.Pkcs7,
        mode: crypto.mode.CBC
      }).toString();
     

      const authData = {
        userid: this.loginForm.get('userName')?.value.toLowerCase(),
        password: pwd
      }

      
      this.authService.userAuthentication(authData)
        .subscribe(res => {
          this.isPasswordErr = false;
         
          if (res.hasOwnProperty('message') && res.message == "Change User Password") {
            const dataToSave = {
              UserCorpId: this.loginForm.get('userName')?.value.toLowerCase(),
              UserFirstName: "",
              UserLastName: ""
            }
            localStorage.setItem('userData', JSON.stringify(dataToSave));
            this.router.navigate(['/auth/forget-password']);
          }
          else {
            console.log('res ',res[0]);
            console.log('res token ',res[0].token);
            //this.userQuery.updateUserData(res[0]);
            //this.userQuery.updateJwtToken(res[0].token); 
            
            // this.userQuery.getJwtToken()
            // .pipe(take(1))
            // .subscribe(value => console.log('VALUE ',value));

            this.getToken.emit(res[0]);

            const dataToSave = {
              UserCorpId: res[0].UserCorpId,
              UserFirstName: res[0].UserFirstName,
              UserLastName: res[0].UserLastName
            }

            //console.log(this.userQuery.getJwtToken());

        
            localStorage.setItem('userData', JSON.stringify(dataToSave));
            sessionStorage.setItem('authStatus', 'loggedin');
            this.router.navigate(['/home']);            
            this.hideLoader();            
          }
        }, err => {         
          this.formError = true;

          if((err.error.error.message === 'Please enter correct Password.') && (err.error.error.hasOwnProperty('remainingAttempts'))) {
            this.isRemainingAttempts = true;
            this.isPasswordErr = false;
            this.isLocked = false;
            this.userNameError = false;
            this.remainingAttemptsCount = err.error.error.remainingAttempts;
          }
          else if (err.error.error.message === 'Invalid credential. You have been locked out for 2 minutes'){
            this.isPasswordErr = false;
            this.isRemainingAttempts = false;
            this.isLocked = true;
            this.errMsg = err.error.error.message;
            this.userNameError = false;
            this.removeLockStatus()
          }
          else {
            this.errMsg = err.error.error.message;
            this.isLocked = false;
            this.isRemainingAttempts = false;
            this.userNameError = true;
          }
          this.hideLoader();
        });
    }
  }

  removeLockStatus() {
    setTimeout(() => {
      this.isLocked = false;
    }, 2000);
  }

  hideLoader(){
    setTimeout(() => {
      this.isLoaderShow = false;
    }, 10000);
    //this.isLoaderShow = false;
  }

  showLoader(){
    this.isLoaderShow = true;
  }


  ngOnDestroy() {} 

}
