import { Injectable } from '@angular/core';
import { Query } from '@datorama/akita';
import { Observable } from 'rxjs';
import { UserStore, UserState } from './user.store';

@Injectable({ providedIn: 'root' })
export class UserQuery extends Query<UserState> {

  constructor(
    protected userStore: UserStore
  ) {
    super(userStore);
  }

  // get user
  getUserDetail(): Observable<any> {
    return this.select(state => state.user);
  }

  // get user data loading status
  getUserLoadingStatus(): Observable<any> {
    return this.select(state => state.isLoaded)
  }

  // get CSRF token
  getCsrfToken(): Observable<string> {
    return this.select(state => state.csrfToken);
  }

  // get JWT token
  getJwtToken(): Observable<string> {
    return this.select(state => state.jwtToken);
  }

  // update user loading status
  updateUserLoadingStatus(status: boolean): Observable<boolean> {
    return this.userStore.update(state => {
      return {
        isLoaded: status
      }
    })
  }

  // update user data
  updateUserData(user = {}) {
    this.userStore.update(state => {
      return {
        user: user,
        isLoaded: true
      }
    })
  }

  // update CSRF token
  updateCsrfToken(token: string) {
    this.userStore.update(state => {
      return {
        csrfToken: token
      }
    })
  }

  // update JWT token
  updateJwtToken(token: string) {
    console.log('updupdateJwtToken token ', token);
    return this.userStore.update(state => {
      return {
        jwtToken: token
      }
    })
  }

}
